.form {
  text-align: center;
}

.input {
  border-radius: 5px;
  margin-right: 10px;
}

.btn {
  border-radius: 4px;
}
.btn:hover,
.btn:focus {
  background-color: burlywood;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0px;
}

.item {
  width: 100%;
  display: flex;
  flex-basis: calc(100% / 5 - 20px);
  margin: 10px;
  transition: all 250ms ease-in;

}

.item:hover,
.item:focus {
  transform: scale(1.1);
}

.link {
  text-decoration: none;
  color: black;

  display: flex;
  flex-direction: column;
  text-align: center;
}

.img {
  object-fit: cover;
    display: block;
    width: 100%;
    height: 321px;
    border-radius: 8px;
    box-shadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.75);
 
}

.title {
  text-align: center;
  color: red;
  text-shadow: 1px 1px 2px #32a895;
}

.text {
  color: red;
  text-shadow: 1px 1px 2px #32a895;
  font-weight: bold;
  text-align: center;
}
